export class ItemsData {
    constructor(events) {
        this.events = events;
        this._preview = null;
    }
    getItem(id) {
        const foundItem = this._items.find(item => item.id === id);
        return foundItem ? foundItem : null;
    }
    get items() {
        return this._items;
    }
    set items(data) {
        this._items = data;
    }
    get preview() {
        return this._preview;
    }
    set preview(id) {
        if (!id) {
            this._preview = null;
            return;
        }
        const selectedItem = this.getItem(id);
        if (selectedItem) {
            this._preview = id;
            this.events.emit('item:selected');
        }
    }
}
