import { CDN_URL } from "../utils/constants";
import { Component } from "./base/Component";
export class Item extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._button = this.container.querySelector('.card__button');
        this._category = this.container.querySelector('.card__category');
        this._title = this.container.querySelector('.card__title');
        this._image = this.container.querySelector('.card__image');
        this._price = this.container.querySelector('.card__price');
        this._description = this.container.querySelector('.card__text');
        this._index = this.container.querySelector('.basket__item-index');
        if (this._index) {
            this._index.textContent = '-';
        }
        if (this.container.classList.contains('gallery__item')) {
            this.container.addEventListener('click', () => {
                this.events.emit('item:select', { item: this });
            });
        }
        if (this._button) {
            if (this._button.classList.contains('basket__item-delete')) {
                this._button.addEventListener('click', () => {
                    this.events.emit('item:delete', { item: this });
                });
            }
            else {
                this._button.addEventListener('click', () => {
                    this.events.emit('item:add', { item: this });
                });
            }
        }
    }
    deleteItem() {
        this.container.remove();
        this.container = null;
    }
    set id(data) {
        this._id = data;
    }
    setValid(isValid) {
        if (this._button)
            this._button.disabled = !isValid;
    }
    setButtonTitle(title) {
        if (this._button && title)
            this._button.textContent = title;
        if (this._button && !title)
            this._button.textContent = 'В корзину';
    }
    set index(data) {
        if (this._index)
            this._index.textContent = data.toString();
    }
    set description(data) {
        if (this._description)
            this._description.textContent = data;
    }
    set image(data) {
        if (!data.includes('/')) {
            data = ''.concat('/', data);
        }
        if (this._image) {
            this._image.src = `${CDN_URL}${data}`;
        }
    }
    set title(data) {
        if (this._title)
            this._title.textContent = data;
    }
    set category(data) {
        if (this._category) {
            this._category.textContent = data;
            this.setCategoryStyle(data);
        }
    }
    setCategoryStyle(data) {
        this._category.className = 'card__category';
        switch (data) {
            case 'софт-скил':
                this._category.classList
                    .add('card__category_soft');
                break;
            case 'другое':
                this._category.classList
                    .add('card__category_other');
                break;
            case 'дополнительное':
                this._category.classList
                    .add('card__category_additional');
                break;
            case 'кнопка':
                this._category.classList
                    .add('card__category_button');
                break;
            case 'хард-скил':
                this._category.classList
                    .add('card__category_hard');
                break;
        }
    }
    set price(data) {
        if (this._price) {
            if (data) {
                this._price.textContent = `${data.toString()} синапсов`;
            }
            else {
                this._price.textContent = 'Бесценный';
                this.setValid(false);
            }
        }
    }
    get id() {
        return this._id;
    }
}
