export class AppApi {
    constructor(baseApi) {
        this._baseApi = baseApi;
    }
    getItems() {
        return this._baseApi.get(`/product`).then((data) => data.items);
    }
    getItem(id) {
        return this._baseApi.get(`/product/${id}`).then((item) => item);
    }
    createOrder(data) {
        return this._baseApi.post(`/order`, data).then((res) => res);
    }
}
