export class OrderData {
    constructor(events) {
        this.events = events;
    }
    isValid() {
        if (this.payment &&
            this.address &&
            this.email &&
            this.phone &&
            this.items &&
            this.total)
            return true;
        else
            return false;
    }
    clear() {
        this._total = null;
        this._items = null;
        this._payment = null;
        this._address = null;
        this._email = null;
        this._phone = null;
        this.events.emit('order:changed');
    }
    get order() {
        return {
            payment: this._payment,
            email: this._email,
            phone: this._phone,
            address: this._address,
            total: this._total,
            items: this._items,
        };
    }
    get payment() {
        return this._payment;
    }
    set payment(data) {
        this._payment = data;
        this.events.emit('order:changed');
    }
    get address() {
        return this._address;
    }
    set address(data) {
        this._address = data;
        this.events.emit('order:changed');
    }
    get email() {
        return this._email;
    }
    set email(data) {
        this._email = data;
        this.events.emit('order:changed');
    }
    get phone() {
        return this._phone;
    }
    set phone(data) {
        this._phone = data;
        this.events.emit('order:changed');
    }
    get total() {
        return this._total;
    }
    set total(data) {
        this._total = data;
        this.events.emit('order:changed');
    }
    set items(data) {
        this._items = data;
        this.events.emit('order:changed');
    }
    get created() {
        return this._created;
    }
    set created(data) {
        if (data.id)
            this._created = data;
        else
            throw new Error('Error while creating the order: id is missing');
    }
}
